			<p class="p">通过从本地或着远程相册选择需要上传的图片</p>
			<pre>
				<code class="hljst">&lt;template&gt;
  &lt;@child-data="getData" album>&lt;/album&gt;
&lt;/template&gt;
&lt;script&gt;
