			      prop="date"
			      label="参数"
			      >
			    </el-table-column>
			    <el-table-column
			      prop="name"
			      label="说明"
			      >
			    </el-table-column>
			    <el-table-column
			      prop="address"
			      label="类型"
			    >
			    </el-table-column>
			  </el-table>
		</div>
	</div>
</template>
<script>
export default{
	data(){
		return{
			tableData:[{
				date: '@child-data',
          		name: '获取后端返回的url',
          		address: 'function(data)'
			}]
		}
	}
}
</script>
