import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/components/index'
import Install from '@/components/modules/install'
import Album from '@/components/modules/album'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Index',
      component: Index,
      redirct:"/install",
      children:[
      	{
      		path: 'install',
      		component: Install
      	},{
          path: 'album',
          component: Album
        }
      ],
    }
  ]
})



